import { useContext, useState, useEffect } from 'react';
import { pemStorage, pemFirestore } from '../firebase/config';
import { UserContext } from "../providers/UserProvider";


const useStorage = (file, dceId, scNr ) => {
  const [progress, setProgress] = useState(0);
  const [error, setError] = useState(null);
  const [url, setUrl] = useState(null);

  const user = useContext(UserContext);
  const uid = user.uid;

  useEffect(() => {
    // references
    const storageRef = pemStorage.ref(file.name);
    const collectionRef = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios');
    
    storageRef.put(file).on('state_changed', (snap) => {
      let percentage = (snap.bytesTransferred / snap.totalBytes) * 100;
      setProgress(percentage);
    }, (err) => {
      setError(err);
    }, async () => {
      const url = await storageRef.getDownloadURL();
    //   await collectionRef.doc(scNr).set({data: {scImg: url}},{merge: true});
      setUrl(url);
    });
  }, [file]);

  return { progress, url, error };
}

export default useStorage;