import React, { useContext, useState } from "react";
import { UserContext } from "../providers/UserProvider";
import { pemFirestore } from "../firebase/config";

export default function NewDce({showDceList, showNewDceForm, dceNameList}) {
    const [inputList, setInputList] = useState([{ featureName: null, featureValue1: null, featureValue2: null }]);
    const [dceName, setDceName] = useState([{ dceName: ""}]);
    const [dceDescr, setDceDescr] = useState([{dceDescr: ""}]);
    const [groupChecked, setGroupChecked] = useState(true);//set to false when allowing ungrouped dce's
    const [groupValue, setGroupValue] = useState(null);
    const [nrQuestions, setNrQuestions] = useState(0);

    const user = useContext(UserContext);
    const uid = user.uid;

    //Check if feature names and values are all filled in before save
  const featuresNotNull = (item)=>{
if(item.featureName==null||item.featureName===""){
alert("Please fill in all feature names");
return false;
}
if(item.featureValue1==null||item.featureValue1===""||item.featureValue2==null||item.featureValue2===""){
  alert("Please fill in all feature values");
  return false;
}
return true;
  }

//helper for dceNotNull check
  const existsName = (name => dceName[0].dceName===name);

//Check if dce name and number of questions are all filled in before save. Also check if dce name is unique
  const dceNotNull = ()=>{
    if(dceName[0].dceName==="" || dceNameList.some(existsName)){
      alert("Fill in a unique DCE name");
      return false;
    }
    if(nrQuestions<=0){
      alert("Fill in the number of questions the participant has to answer in the DCE");
      return false;
    }
    return true;
  }

  //Check if groupValue is not null 
  const groupValueNotNull = ()=>{
    if (groupValue===null){
      alert("Please select a value for the group feature");
      return false;
    }
    return true;
  }

  //Save dce if all fields are filled in
    const saveDce = (e)=>{
        e.preventDefault();
        if(inputList.every(featuresNotNull)&&dceNotNull()&&groupValueNotNull()){
        let dceColl = pemFirestore.collection('users').doc(uid).collection('DceList');
        dceColl.add({name: dceName[0], descr: dceDescr[0], features: inputList, nrFeatures: inputList.length, grouped: groupChecked, groupFeature: groupValue, nrQuestions: nrQuestions})
        .then((docRef)=>{
              console.log("Document written with ID: ", docRef.id);
        })
        .catch((error) => {
            console.error("Error adding document: ", error);
        });
        showDceList();
        showNewDceForm();
    }
  }

    // handle name input change
    const handleNameInputChange = (e) => {
        const name = e.target.value;
        const nameList = [...dceName];
        nameList[0]["dceName"] = name;
        setDceName(nameList);
    }

    // handle descr input change
    const handleDescrInputChange = (e) => {
        const descr = e.target.value;
        const descrList = [...dceDescr];
        descrList[0]["dceDescr"] = descr;
        setDceDescr(descrList);
    }

    // handle nr questions input change
    const handleNrQuestionsInput = (e) =>{
      const nrString = e.target.value;
      let nrQ = 0;
      nrQ = Number(nrString);
      setNrQuestions(nrQ);
    }

     // handle feature input change
  const handleInputChange = (e, index) => {
    const { name, value } = e.target;
    const list = [...inputList];
    list[index][name] = value;
    setInputList(list);
  };
 
  // deletes dce from database and refreshes list
  const handleRemoveClick = index => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
    showDceList();
  };
 
  // Adds another feature in the form
  const handleAddClick = () => {
    setInputList([...inputList, { featureName: null , featureValue1: null, featureValue2: null}]);
  }

  // checkbox for group feature - prep for ungrouped dce's
  const handleCheckbox = (e)=>{
    const { checked} = e.target;
    setGroupChecked(checked);
  }

  //registers choice for group feature
  const handleGroupChoice = (e) =>{
    const fName = e.target.value;
    setGroupValue(fName);
  }

  // function to create options in group feature in html
  let featureList = inputList.map((x, i)=>{
    return(
      <option key={i} value={x.featureName}>{x.featureName}</option>
    )
  })
 
  //html to show in browser
  return (
        <div>
            <form className="form-inline">
                <div className="form-floating">
                    <input type="text" className="form-control" name="dceName"  placeholder="DCE name" id="dceName" 
                    required onChange={handleNameInputChange}></input>
                    <label htmlFor="dceName" className="block">Name: </label>
                    </div>
                <div className="form-floating">
                    <input type="text" className="form-control" name="descr"  placeholder="Description - this text will be displayed in the DCE" id="descr" 
                    required onChange={handleDescrInputChange}></input>
                    <label htmlFor="descr" className="block">Description (shown in DCE): </label>
                    </div>
                  
                {inputList.map((x, i) => {
        return (
          <div className=" row">
              <div className="col-xs-12 col-sm-9 form-floating">
            <input
            className="form-control "
              name="featureName"
              required
              id={i}
   placeholder="Enter feature name"
              value={x.featureName}
              onChange={e => handleInputChange(e, i)}
            />             
            <label htmlFor={i} className="">Feature name: </label>
            </div>
            <div className="col col-xs-1">
            {inputList.length !== 1 && <button
                className="btn btn-sm btn-outline-success"
                onClick={() => handleRemoveClick(i)}><i class="material-icons mat-icon">delete_forever</i></button>}
              </div>

              <div className="col-xs-12 col-sm-9 form-floating">
              <input
            className="form-control "
              name="featureValue1"
              required
              id={i}
              placeholder="Enter feature value 1"
              value={x.featureValue1}
              onChange={e => handleInputChange(e, i)}
            />             
            <label htmlFor={i} className="">Feature value 1: </label>
            </div>
              <div className="col-xs-12 col-sm-9 form-floating">
              <input
            className="form-control "
              name="featureValue2"
              required
              id={i}
              placeholder="Enter feature value 2"
              value={x.featureValue2}
              onChange={e => handleInputChange(e, i)}
            />             
            <label htmlFor={i} className="">Feature value 2: </label>
            </div>
              <div>
              {inputList.length - 1 === i && <button className="btn btn-sm btn-outline-info" onClick={handleAddClick}>Add another feature</button>}</div>
           </div>
        );
      })}
      <div>
        <label htmlFor="nrChoices">Nr of questions in DCE</label>
        <input type="number" className="col col-xs-1" id="nrChoices" required onChange={handleNrQuestionsInput} />
      </div>
      {/* uncomment the checkbox part when allowing ungrouped dce's */ }
      {/* <div>
        <input type="checkbox" name="group" id="chkGrp" onClick={handleCheckbox}></input>
        <label htmlFor="group" className="">Group by feature </label>
      </div> */}
      <div>
        {groupChecked&&
        <label htmlFor="groupFeature" className="col-xs-6 col-form-label">Group feature
            <select className="form-select form-select-sm col-xs-6 " defaultValue="" name="groupFeature"  id="groupFeature"  onChange={handleGroupChoice}>
            <option value="" disabled></option>
             {featureList}
             </select>
            </label>
        }
      </div>
      <button className="btn btn-sm btn-outline-success" onClick={saveDce} >Save DCE</button>
          </form>
        </div>
    )
}
