import React, {useState} from 'react';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AppBar from '@material-ui/core/AppBar';
import SwipeableViews from 'react-swipeable-views';
import OptieA from './OptieA';
import OptieB from './OptieB';
import '../index.css';



// class MenuTabs extends React.Component {
  const MenuTabs = ({handleClick, sc1, sc2, dceDescr})=>{
    // const [answerList, setAnswerList] =useState([]);

    const [index, setIndex] = useState(0);
    const [value, setValue] = useState(0);
    

  const handleChange = (event, newValue) => {
    setValue(newValue)
    setIndex(
      newValue,
    );
    };

  const handleChangeIndex = (index) => {
    setIndex(
      index,
    );
    setValue (index);
  };

  const styles = {
    tabs: {
       background: '#fff',
     },
     label: {
       fontSize: '100%',
     }}

    return (
      <div className="swiper-container">
          <AppBar position="static" color="default">
        <Tabs indicatorColor="primary" textColor="primary" background="primary"
        variant="fullWidth" value={value} onChange={handleChange} style={styles.tabs}>
         <Tab style={styles.label} label="option n°1" />
          <Tab style={styles.label} label="option n°2" />
       </Tabs>
       </AppBar>
       <h5 class="text-secondary mt-1 mb-0 text-center">{dceDescr}</h5>

        <SwipeableViews enableMouseEvents index={index} onChangeIndex={handleChangeIndex}>
          <div className="slide">
             <div className="innerslide" >
             <OptieA handleClick={handleClick} sc1={sc1} sc2={sc2}/>
             </div> 
              </div>
          <div className="slide">
          <div className="innerslide" >
              <OptieB handleClick={handleClick} sc1={sc1} sc2={sc2}/>
             </div> 
                      </div>
        </SwipeableViews>
      </div>
    );
  }


export default MenuTabs;