import React, { useContext, useEffect, useState } from "react";
import { UserContext } from "../providers/UserProvider";
import { pemFirestore, pemId } from "../firebase/config";
import NewAspect from "./NewAspect";
import AspectEdit from "./AspectEdit";
import { HashRouter as Router, Route, Link } from "react-router-dom";
import { useParams, useLocation} from "react-router-dom";



export default function AspectList() {

    const [aspects, setAspects] = useState([]);
    const [aspectsObjs, setAspectsObjs] = useState([])
    const [chosenFeatures, setChosenFeatures] = useState([]);
    const [aspectFilters, setAspectFilters] = useState([]);
    const [groupValues, setGroupValues] = useState([])
    const [showNew, setShowNew] = useState(false);
    const [aspectNameEdit, setAspectNameEdit] = useState('(Click on ICON in Aspect list)');
    const [aspectDescr, setAspectDescr] = useState('');
    const [aspectId, setAspectId] = useState('');
    const [aspectNameList, setAspectNameList] = useState([]);
    const user = useContext(UserContext);
    const {userId, dceId } = useParams();
    const {dceNameEdit} = useLocation();

console.log('location data in aspectList: '+ JSON.stringify(useLocation()))
    console.log("user uid in dcelist is: " +JSON.stringify(user.uid));

//Get list of dce's from database and show on screen
const showAspectList = async() => {
    let response = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList');
    let data = await response.get();
    let tempDocs = [];
    // let tempNameList = [];
    data.docs.forEach(doc => {
        tempDocs.push({...doc.data(), id: doc.id});
        // tempNameList.push(doc.data().name.dceName);
      });
      console.log('Aspects data: '+JSON.stringify(tempDocs));
    setAspects(tempDocs) ;
    // setDceNameList(tempNameList);
}

//auto trigger showAspectList upon first render only
useEffect(() => {
    showAspectList();
  }, [dceNameEdit])


// collect aspect id from clicked button and get data from database so aspect can be edited.
const editAspect = (e) => {
    let aspect;
    
console.log("e is: "+ e.target.parentElement.parentElement.getAttribute('data-id'));
if(e.target.parentElement.parentElement.getAttribute('data-id')){
aspect = e.target.parentElement.parentElement.getAttribute('data-id');
} else{
    aspect = e.target.parentElement.getAttribute('data-id');
}
loadAspectData(aspect);
}

const loadAspectData = (aspect)=>{
    let emptyChosen = [];
    const aspectData = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList').doc(aspect);
    aspectData.get()
    .then((doc)=>{
        const aspectName = doc.data().aspectName;
        console.log("docData: "+JSON.stringify(doc.data()));
        let combis = [];
        let objCombis =[];
        let tempGValues = [];
        aspectData.collection('Combis').get()
        .then((snapshot)=>{
            snapshot.docs.forEach(snap=>{
                console.log("snap: "+JSON.stringify(snap.data().filters));
             combis.push(snap.data().filters);
             objCombis.push({aspectName: aspectName, combis: {nr: snap.data().nr, groupValue: snap.data().groupValue, filters: snap.data().filters}});
             emptyChosen.push([]);
             tempGValues.push(snap.data().groupValue)
            })
            console.log("objCombis: "+JSON.stringify(objCombis));
            console.log("emptyChosen: "+JSON.stringify(emptyChosen));
            console.log("tempGValues: "+JSON.stringify(tempGValues));
            setAspectDescr(doc.data().aspectDescr);
            setGroupValues(tempGValues);
            setAspectsObjs(objCombis);
            setAspectFilters(combis);
            setChosenFeatures(emptyChosen);
        })
        setAspectNameEdit(aspectName);
        setAspectId(aspect);
    })    
}

const saveFilters = (cIndex, filters, emptyChosen, groupValues)=>{
    console.log('params: userId: '+ userId+' dceId: '+dceId+ ' aspectId: '+aspectId+ ' cIndex: '+cIndex);
    console.log('cINdex: '+cIndex+ 'filters: '+ JSON.stringify(filters)+' emptyChosen: '+JSON.stringify(emptyChosen));
    const aspectRef = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList').doc(aspectId).collection('Combis').doc(cIndex);
    aspectRef.update({filters: filters, groupValue: groupValues[cIndex]})
.then(() => {
    let tempFilters = [...aspectFilters];
    tempFilters[cIndex] = filters;
    setAspectFilters(tempFilters);
    setChosenFeatures(emptyChosen);
    console.log("Document successfully updated!");
})
.catch((error) => {
    // The document probably doesn't exist.
    console.error("Error updating document: ", error);
});
}

const handleAddCombi = async()=>{
    let response = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList').doc(aspectId).collection('Combis').orderBy('nr', 'asc');
    response.get()
    .then((data)=>{console.log('data in handleAddCombi: '+JSON.stringify(data.docs));
    let seqNr=0;
    for(let i=0; i<data.docs.length; i++){
        if(data.docs[i].data().nr!==seqNr){
            console.log("doc nr !== seqNr, seqNr is: "+seqNr + "doc id is: "+ data.docs[i].data().nr);
            setCombi(seqNr);
            return;
        }
        else  {
            console.log("doc nr == seqNr, seqNr is: "+seqNr + "doc id is: "+ data.docs[i].data().nr);
            seqNr +=1;
            if(i===data.docs.length-1){
                console.log('Last doc, i===length');
                setCombi(seqNr);
            }
        }
    }
})
}


const setCombi = (seqNr)=>{
    pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList').doc(aspectId).collection('Combis').doc(seqNr.toString())
    .set({nr: seqNr, groupValue: "", filters: []})
    .then(()=>{
        console.log('combi added');
        loadAspectData(aspectId);
    })
    .catch((error)=>{
        console.log('Error adding combi', error);
    })
}


const handleDeleteCombi = (e, cIndex)=>{
    pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList').doc(aspectId).collection('Combis').doc(cIndex.toString())
    .delete().then(() => {
        console.log("Combination successfully deleted!");
        loadAspectData(aspectId);
    }).catch((error) => {
        console.error("Error removing document: ", error);
    });        
}

//delete a aspect from database and refresh list
const deleteAspect = (e) => {
    let aspect;
    if(e.target.parentElement.parentElement.getAttribute('data-id')){
    aspect = e.target.parentElement.parentElement.getAttribute('data-id');
    } else{
        aspect = e.target.parentElement.getAttribute('data-id');
    }    
    let aspectRef = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList').doc(aspect);
    aspectRef.collection('Combis').get()
    .then((docs)=>{
        docs.forEach(doc=>{
            aspectRef.collection('Combis').doc(doc.id).delete()
            .then(()=>{console.log('Combi with ID '+doc.id+ ' deleted')})
        })
    })
    .then(()=>{
        console.log("Combis deleted");
        aspectRef.delete()
    .then(() => {
        console.log("Document with ID"+aspect+ "  successfully deleted!");
    }).catch((error) => {
        console.error("Error removing document: ", error);
    });
    if(aspect===aspectId){
        setAspectId('');
        setAspectDescr('');
        setAspectNameEdit('(Click on ICON in Aspect list)');
        setAspectFilters([]);
        setChosenFeatures([]);    
    }
showAspectList();
})
}
//toggle to show or hide form for new aspect
  const showNewAspectForm = ()=>{
      if(showNew){
          setShowNew(false)
      }
      else if(!showNew){
          setShowNew(true)
      }
  }

  //html to show in browser
    return (
    <div>
        <div className="row my-4">
            <div className="listContainer col col-12 col-sm-3">
           <div className=" col-auto ">
                <h4><u>Aspect list</u></h4>
                <ul>
                    {aspects && aspects.map((aspect, i)=>{
                        return(
                        <div key={i} className="">
                            <h5 className="mt-3">
                                <li className="listRuler mb-1"  data-id={aspect.id}> <b>{aspect.aspectName}</b>
                                    <hr className="ruler"/> 
                                    <button className="btn btn-sm btn-outline-warning" onClick={editAspect}><i class="material-icons mat-icon">edit</i></button>
                                    <button className="btn  btn-outline-success btn-sm mx-2" onClick={deleteAspect}><i class="material-icons mat-icon">delete_forever</i></button>
                                </li>
                            </h5>
                        </div>
                        )
                        })
                    }
                </ul>
            </div>
<div className="row my-4">
<div className="col-auto  ">
    <h4><u>Create new Aspect</u></h4>
    <div> <button className="btn btn-sm btn-outline-info" onClick={showNewAspectForm}> new Aspect</button>
</div>
{showNew&&<NewAspect dceId={dceId} showAspectList={showAspectList} showNewAspectForm={showNewAspectForm} aspectNameList={aspectNameList} />}
</div>
</div>
</div>

<div className="col scenarioContainer">
    <AspectEdit aspectNameEdit={aspectNameEdit} aspectDescr={aspectDescr} aspectFilters={aspectFilters} saveFilters={saveFilters} chosenFeatures={chosenFeatures} setChosenFeatures={setChosenFeatures} handleAddCombi={handleAddCombi} handleDeleteCombi={handleDeleteCombi} aspectsObjs={aspectsObjs} groupValues={groupValues} setGroupValues={setGroupValues}/>
</div>
</div>

</div>
    )
}
