import { useState, useEffect } from "react";

const OptieB = ({handleClick, sc2, sc1}) => {

  const [choiceValue, setChoiceValue] = useState({})

    useEffect(() => {
      if(sc1&&sc2){
      let fValues1 = sc1.data.fValues;
      let fValues2 = sc2.data.fValues;
      const cValue = {pref: fValues2, nonPref: fValues1}
      setChoiceValue(cValue);
      }
    }, [sc1, sc2])

    return ( 
        <div className="swiper-slide choose-btn">
				<div className="s10 ">
        {sc2&&<div className="choice-container">
  <p>{sc2.data.scText}</p>
  <img className = "swipe-img choice-container" src={sc2.data.scImg} alt="Image not loaded"
             />
</div>}
                </div>
						{sc2&&<button value='B' onClick={()=>{handleClick(choiceValue)}}  className="btn blue darken-2 waves-effect waves-light z-depth-2">Choose</button>}
                        </div>
     );
}
 
export default OptieB;