import ResultDce from "./ResultDce";

const EndForm = ({dceDetails, dceNameEdit, dceId, dceFeatures, userId}) => {

    return ( 
       <div className="my-5"> 
         <p>Thank you for completing this experiment!</p>
         <div className="col resultsContainer mx-3">
    <ResultDce  dceDetails={dceDetails} dceNameEdit = {dceNameEdit} dceId={dceId} dceFeatures={dceFeatures} userId={userId} />
</div>
     </div>
     );
}
 
export default EndForm;