import React, { useContext, useState, useEffect } from "react";
import { UserContext } from "../providers/UserProvider";
import { pemFirestore } from "../firebase/config";
import NewScenario from "./NewScenario";
import { Link } from "react-router-dom";


export default function ScenarioList({dceNameEdit, dceId, dceFeatures, dceDetails}) {

    const user = useContext(UserContext);
    const uid = user.uid;
    const [chosen, setChosen] = useState([{nr: 0}]);
    const [scList, setScList] = useState([]);
    const [scNr, setScNr] = useState("");
    const [isSaved, setIsSaved] = useState(false);

    //Show list of scenario's for the selected dce
    const showScList = async() => {
        if(dceId){
        let response = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios').orderBy('data.nr');
        let data = await response.get();
        let tempDocs = [];
        data.docs.forEach(doc => {
            tempDocs.push({...doc.data(), id: doc.id});
            });
        setScList(tempDocs) ;
        }
    }

    //show scenario list when dceId is changed
    useEffect(() => {
        showScList();
        setChosen([{}]);
            setScNr(null);
            setIsSaved(false)
      }, [dceId])

// Adds scenario with id which is first available unique number 
// Features are taken from dce
    const handleAddScenario = async(altnr) =>{
        if(dceId){
            const fValues ={};
            // let featNr = 1;
            dceFeatures.map(feature=>{
                fValues[feature.featureName]="";
                // featNr +=1;
            })
            let response = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios').orderBy('data.nr');
        let data =  await response.get();
        let seqNr=1;
        data.docs.forEach(doc => {
            if(doc.data().data.nr!==seqNr){
                console.log("doc id !== seqNr, seqNr is: "+seqNr + "doc id is: "+ doc.data().data.nr);
                return;
            }
            else {
                console.log("doc id == seqNr, seqNr is: "+seqNr + "doc id is: "+ doc.data().data.nr);
                seqNr +=1;
            }
            });
            let altSc = false;
            if (altnr){altSc = true}
            setChosen([{}]);
            const detailData = {data: {nr: seqNr, altnr: altnr, altSc: altSc, fValues, scText: "", scImg: "", scExpl: ""}};
            let response2 = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios');
            response2.doc(seqNr.toString()).set(detailData);
            setChosen([detailData]);
            setScNr(seqNr);
        showScList();
      return seqNr;
    }
}

//delete scenario from database and refresh list
const deleteSc = (e) => {
    let sc;
    console.log("e is: "+ e.target.parentElement.parentElement.getAttribute('data-id'));
    if(e.target.parentElement.parentElement.getAttribute('data-id')){
    sc = e.target.parentElement.parentElement.getAttribute('data-id');
    } else{
        sc = e.target.parentElement.getAttribute('data-id');
    }
    let altSc = scList.find(scx=>scx.id===sc).data.altnr;
console.log("Alt sc that should be deleted: "+JSON.stringify(altSc));
    
    pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios').doc(sc).delete().then(() => {
        console.log("Scenario with ID "+sc+ "  successfully deleted!");
    }).catch((error) => {
        console.error("Error removing document: ", error);
    });
    if (altSc){
    pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios').doc(altSc.toString()).delete().then(() => {
        console.log("Alt scenario with ID "+altSc.toString()+ "  successfully deleted!");
    }).catch((error) => {
        console.error("Error removing document: ", error);
    });}
if (sc===scNr){
    setChosen([{}]);
    setScNr("");
    setIsSaved(false);
}
showScList();
}

//select scenario to show and edit details 
const editSc = (e) => {
    setChosen([{}]);
    let sc;
    console.log("e is: "+ e.target.parentElement.parentElement.getAttribute('data-id'));
    if(e.target.parentElement.parentElement.getAttribute('data-id')){
    sc = e.target.parentElement.parentElement.getAttribute('data-id');
    } else{
        sc = e.target.parentElement.getAttribute('data-id');
    }
    setScNr(sc);
    pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios').doc(sc).get()
.then((doc)=>{
    let data = doc.data();
    setChosen([data]);
    setIsSaved(false);
 })
}

    //html to show in browser
    return (
        <div>
            <h4><u>Details</u> for <b> {dceNameEdit}</b>{scNr&&<span> - Scenario nr. {scNr}</span>}{!scNr&&<span> - Select a scenario nr.</span>}</h4>
            <div className="">
            <h6>{dceDetails&&<span> <Link to={{pathname:"/app/"+uid+"/"+ dceId}}>
                    <button className="btn btn-sm btn-outline-info mx-1" ><i class="material-icons mat-icon mt-1">play_circle_outline</i></button>
                    </Link></span>}
                    {dceDetails&&<span> Play this DCE</span>} {dceDetails&& <span> <hr className="ruler detailsRuler"/> <button className="btn btn-sm btn-outline-info" onClick={() => {let clipText = window.location.href+"app/"+uid+"/"+ dceId; navigator.clipboard.writeText(clipText)}}>Copy DCE link </button></span>} </h6></div>
           <div className="pb-4"><h6> {dceDetails&&dceDetails.details.grouped&&<span>Group feature: {dceDetails.details.groupFeature}</span>} {dceDetails&& <span> <hr className="ruler detailsRuler"/> Questions in DCE: {dceDetails.details.nrQuestions}</span>}</h6></div>
            <div className="row">
   <div className="scenarioList col col-6 col-md-3 col-sm-5">
    <h5>Scenario's</h5>
    <ul>
    <li className="listRuler mb-1">Nr.  <hr className="ruler"/> Edit <hr className="rulerSm"/>&nbsp;Delete </li>
                {scList && scList.map((sc,i)=>{
                return(
                    <div key={i} className="">
                    <h5 className="mt-3"><li className="listRuler mb-1" data-id={sc.id}><button className="btn btn-sm btn-outline-info" onClick={editSc}>{sc.data.nr}</button>
                    <hr className="ruler"/> 
                    <button className="btn  btn-outline-warning btn-sm mx-2" onClick={editSc} > <i class="material-icons mat-icon">edit</i></button>
                    <hr className="rulerSm"/>
                    <button className="btn  btn-outline-success btn-sm mx-2" onClick={(e) => { if (window.confirm('Are you sure you wish to delete this scenario?')) deleteSc(e) }} > <i class="material-icons mat-icon">delete_forever</i></button></li></h5>
                    </div>
                )
                })
            }
   
           {dceId&&<li className="mb-2"><button className="btn btn-sm btn-outline-info" onClick={()=>handleAddScenario(null)}>Add scenario</button></li>}
    </ul>
    </div>
    
    <div className="col">
        <NewScenario dceId={dceId} dceFeatures={dceFeatures} dceDetails={dceDetails} scNr={scNr} setScNr={setScNr} 
        chosen={chosen} setChosen={setChosen} handleAddScenario={handleAddScenario} setIsSaved={setIsSaved} isSaved={isSaved}/>
    </div>
    </div>
        </div>
    )
}
