import React, {useContext, useState, useEffect} from 'react';
import { pemFirestore, pemId } from "../firebase/config";
import { useParams, useLocation} from "react-router-dom";
import useFirestoreLoadResult from '../hooks/useFirestoreLoadResults';
import { UserContext } from "../providers/UserProvider";


export default function ResultDce({dceId, dceNameEdit, dceDetails, dceFeatures, userId}) {
    // const {userId, dceId } = useParams();
    
    const [resultHtml, setResultHtml] = useState([]);
    const [aspects, setAspects] = useState([]);
    const [aspectObjs, setAspectObjs] = useState([]);
    const location = useLocation();
    // const user = useContext(UserContext);
    console.log('user and dce id in ResultDce: '+JSON.stringify(userId) +' '+dceId)
    console.log('dceDetails in ResultDce start: '+JSON.stringify(dceDetails))
    const {results} = useFirestoreLoadResult(userId ,dceId);
    const [ind, setInd] = useState(null);
    const {details} = dceDetails;
    // const [clNames, setClNames] = useState("progress w-80 mx-auto")

    
    useEffect(() => {
        if(dceId&&details){
            console.log('DceId in useEffect: '+dceId)
        let tempDocs = [];
        let tempAspectObjs = [];
        let response = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList');
        response.get()
        .then((querySnapshot)=>{
            // console.log('Aspects just loaded: '+JSON.stringify(querySnapshot))
            querySnapshot.forEach((doc)=>{
                // setInd(indx);
                console.log('Aspects just loaded, in docs.forEach, doc: '+JSON.stringify(doc.data()))
                tempAspectObjs.push({aspectName: doc.data().aspectName, aspectDescr: doc.data().aspectDescr, id: doc.id});
                pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('AspectList').doc(doc.id).collection('Combis')
                .get()
                .then((snapshot)=>{
                    console.log('tempAspectObjs at start: '+JSON.stringify(tempAspectObjs))
                    let tempFilters = [];
                     let tempAltFilters = [];
                    snapshot.forEach((snap)=>{
                        console.log('doc.is is: '+ doc.id)
                        let tempFilter = snap.data();
                        let favorFilters = [];
                        let againstFilters = [];
                        favorFilters.push(...tempFilter.filters);
                        againstFilters.push(...tempFilter.filters);
                        // console.log('favorFilters is: '+JSON.stringify(favorFilters.filters.find(obj=>obj.fName===details.groupFeature).fValue))
                        // const altObj = favorFilters.filters.find(obj=>obj.fName===details.groupFeature);
                        // let altValue = altObj.fValue;
                        console.log('favorFilters is : '+JSON.stringify(favorFilters))
                        let newFavorFilter = {fName: details.groupFeature, fValue: snap.data().groupValue};
                        favorFilters.push(newFavorFilter);
                        let newAgainstValue = "";
                        dceFeatures.map((feature,i) =>{
                        console.log("Feature: "+ JSON.stringify(feature.featureName));
                        if (feature.featureName===details.groupFeature){
                            console.log("Group feat ===featureName")
                            if(feature.featureValue1===snap.data().groupValue){
                                newAgainstValue = feature.featureValue2;}
                            if(feature.featureValue2===snap.data().groupValue){
                                newAgainstValue = feature.featureValue1;}
                        }
                        })
                        let newAgainstFilter = {fName: details.groupFeature, fValue: newAgainstValue};
                        againstFilters.push(newAgainstFilter);
                        console.log('tempFilter: '+JSON.stringify(tempFilter))
                        tempFilter['favorFilters'] = favorFilters;
                        tempFilter['againstFilters'] = againstFilters
                        console.log('tempFilter: '+JSON.stringify(tempFilter))
                        console.log('groupFeature: '+ details.groupFeature+ ' favorFilters: '+JSON.stringify(favorFilters)+ '  againstFilters: '+JSON.stringify(againstFilters))
                        tempFilters.push(tempFilter);;
                        console.log('Combis in snap: '+JSON.stringify(tempFilters)+JSON.stringify(doc.id))
                        // tempDocs.push({aspectName: doc.data().aspectName,id: doc.id, combi: snap.data()})
                    })
                    console.log('doc.is is: '+ doc.id)
                    let newTempObjs = [...tempAspectObjs]
                        let index = tempAspectObjs.findIndex(obj=>obj.id===doc.id);
                        console.log('index2: '+JSON.stringify(index))
                        newTempObjs[index]['combis'] = tempFilters;
                        console.log('ind: '+ind)
                        console.log('newtempObjs before setting: '+JSON.stringify(newTempObjs))
                        setAspectObjs(newTempObjs);
                        })
            })
         })
        return
    }},[dceId])


    useEffect(() => {
        let tempResult = [];
        console.log('all aspectObj is: '+JSON.stringify(aspectObjs))
        aspectObjs.forEach((aspectObj)=>{
            let aspectName = aspectObj.aspectName;
            let aspectDescr = aspectObj.aspectDescr;
            let oddsFavor = [];
            let oddsAgainst = [];
            console.log('aspectObj is: '+JSON.stringify(aspectObj));
            if(aspectObj.combis){
                console.log('combi if entered')
            aspectObj.combis.forEach(combi=>{
                console.log('combi: '+JSON.stringify(combi));

                let nr = combi.nr;

                    const checkFValue = (result=>{
                        let passesAllFilters = true;
                        combi.favorFilters.forEach(favFilter=>{
                            console.log('forEach favFilter: '+ JSON.stringify(favFilter))
                    console.log('result in forEach: '+ JSON.stringify(result))
                    if(!(favFilter.fValue===result[favFilter.fName]&&result.hasOwnProperty(favFilter.fName))){
                        passesAllFilters = false;
                    }
                })
                    return(passesAllFilters)
                    })
                    let tempFavors = results.filter(checkFValue);
                    tempFavors.forEach(favor=>{
                        console.log(' forEach favor '+JSON.stringify(favor) )
                        if(oddsFavor.indexOf(favor)===-1){
                            oddsFavor.push(favor);
                        }
                    })
               
                
                    const checkAValue = (result=>{
                        let passesAllFilters = true;
                        combi.againstFilters.forEach(againstFilter=>{
                        console.log('forEach againstFilter: '+ JSON.stringify(againstFilter))
                        console.log('result in forEach: '+ JSON.stringify(result))
                        if(!(againstFilter.fValue===result[againstFilter.fName]&&result.hasOwnProperty(againstFilter.fName))){
                            passesAllFilters=false;
                        }
                    })
                        return(passesAllFilters)
                    })
                    let tempAgainst = results.filter(checkAValue);
                    tempAgainst.forEach(against=>{
                        console.log(' forEach against '+JSON.stringify(against) )
                        if (oddsAgainst.indexOf(against)===-1){
                            oddsAgainst.push(against);
                        }
                        console.log('oddsAgainst: '+JSON.stringify(oddsAgainst))
                    })
                })
        }
        let aspectOdds = 1;
        if (oddsAgainst.length>0){
        aspectOdds = oddsFavor.length/oddsAgainst.length
        }
        let logAspectOdds = sigmoid(aspectOdds);
        let prob = 2*(oddsFavor.length/(oddsFavor.length+oddsAgainst.length))-1;
        let probPerc = prob*100;
        let graphProb = Math.abs(prob*50);
       let clNames = graphReverse(prob);
       let barClasses = getBarClasses(prob)

                              console.log('oddsFavor: '+JSON.stringify(oddsFavor))
                tempResult.push(
                <div>
                    Aspect: {aspectName} <br/>
                    <span className="text-secondary">{aspectDescr}</span><br/>
                    {/* Nr: {nr}  */}
                    {/* Length in favor: {oddsFavor.length} <br/> */}
                    {/* Length against odds: {oddsAgainst.length}<br/> */}
                    {/* Odds: {aspectOdds}<br/> */}
                    {/* Prob: {probPerc.toFixed(2)} %<br/> */}
                    <div className={clNames}>
                    <div className="progress-bar bg-light-grey" role="progressbar" style={{width: 50+"%"}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                    {prob===0&&<div class="progress-bar bg-teal" role="progressbar" style={{width: 2 +"px"}} aria-valuenow="30" aria-valuemin="0" aria-valuemax="100"></div>}
<div className={barClasses} role="progressbar" aria-valuenow={prob} aria-valuemin="0" aria-valuemax="100" style={{width:graphProb+"%"}}>
    {(prob>=0.1||prob<=-0.1)&&<span className="sr-only">&Delta; P = {prob.toFixed(2)}</span>}
    </div>
  </div>
  {(prob<0.1&&prob>-0.1)&&<div>&Delta; P = {prob.toFixed(2)}</div>}
                    {/* Sigmoid: {logAspectOdds}<br/> */}
                    {/* elements in favor: {JSON.stringify(oddsFavor)}<br/> */}
                    {/* elements against: {JSON.stringify(oddsAgainst)}<br/> */}

                </div>)
                console.log('resultHtml: '+JSON.stringify(tempResult))
                setResultHtml(tempResult);
            })
                return 
    }, [aspectObjs])

    const sigmoid = (t=> {
        return 1/(1+Math.pow(Math.E, -t));
    })

    const graphReverse = (prob)=>{
        let tempClNames = "progress w-80 mx-auto"
        if (prob<0){
            tempClNames="progress w-80 mx-auto flex-row-reverse"
        }
        console.log("clNames: "+tempClNames);
        return  (tempClNames);
    }

    const getBarClasses = (prob)=>{
        let barClasses = "progress-bar bg-teal progress-bar-striped";
        if (prob<0){
            barClasses = "progress-bar bg-purple progress-bar-striped"
        }
        return barClasses;
    }

    return (
        <div>
             <h4>Some of the results for the experiment so far:</h4>
            {/* Results: {JSON.stringify(results)} */}
            {/* filter results: {JSON.stringify(results.filter(checkValue, featToCheck))}<br/> */}
           <div className="mb-3"> &Delta; P shows the difference in probability that the aspect is true and the 
               probability that the aspect is not true
           </div>
         {resultHtml}
            {/* dceFeatures: {dceFeatures&&JSON.stringify(dceFeatures)} */}
            
           
        </div>
    )
}
