import React, { useContext, useEffect, useState } from "react";
import { UserContext } from "../providers/UserProvider";
import { pemFirestore } from "../firebase/config";
import NewDce from "./NewDce";
import ScenarioList from "./ScenarioList";
import { HashRouter as Router, Route, Link } from "react-router-dom";


export default function DceList() {

    const [dceHtml, setDceHtml] = useState([]);
    const [showNew, setShowNew] = useState(false);
    const [dceNameEdit, setDceNameEdit] = useState('(Click on ICON in DCE list)');
    const [dceFeatures, setDceFeatures] = useState([]);
    const [dceId, setDceId] = useState('');
    const [dceNameList, setDceNameList] = useState([]);
    const [dceDetails, setDceDetails] = useState(null)
    const user = useContext(UserContext);
    const uid = user.uid;

    console.log("user uid in dcelist is: " +JSON.stringify(user.uid));

//Get list of dce's from database and show on screen
const showDceList = async() => {
    let response = pemFirestore.collection('users').doc(uid).collection('DceList');
    let data = await response.get();
    let tempDocs = [];
    let tempNameList = [];
    data.docs.forEach(doc => {
        tempDocs.push({...doc.data(), id: doc.id});
        tempNameList.push(doc.data().name.dceName);
      });
    setDceHtml(tempDocs) ;
    setDceNameList(tempNameList);
}

//auto trigger showDceList upon first render only
useEffect(() => {
    showDceList();
  }, [])


// collect dce id from clicked button and get data from database so dce can be edited.
const editDce = (e) => {
    let dce;
console.log("e is: "+ e.target.parentElement.parentElement.getAttribute('data-id'));
//get parent element as well as parent of parent to avoid misclick on icon
if(e.target.parentElement.parentElement.getAttribute('data-id')){
dce = e.target.parentElement.parentElement.getAttribute('data-id');
} else{
    dce = e.target.parentElement.getAttribute('data-id');
}
const dceData = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dce).get()
.then((doc)=>{
    const dceName = doc.data().name.dceName;
    const tempDetails = {details: {grouped: doc.data().grouped, groupFeature: doc.data().groupFeature, nrQuestions: doc.data().nrQuestions}}
    setDceNameEdit(dceName);
    setDceFeatures(doc.data().features)
    setDceId(dce);
    setDceDetails(tempDetails);
})
}

//delete a dce from database and refresh list
const deleteDce = (e) => {
    let dce;
    if(e.target.parentElement.parentElement.getAttribute('data-id')){
    dce = e.target.parentElement.parentElement.getAttribute('data-id');
    } else{
        dce = e.target.parentElement.getAttribute('data-id');
    }    pemFirestore.collection('users').doc(uid).collection('DceList').doc(dce).delete().then(() => {
        console.log("Documentwith ID"+dce.id+ "  successfully deleted!");
    }).catch((error) => {
        console.error("Error removing document: ", error);
    });
showDceList();
}

//toggle to show or hide form for new dce
  const showNewDceForm = ()=>{
      if(showNew){
          setShowNew(false)
      }
      else if(!showNew){
          setShowNew(true)
      }
  }

  //html to show in browser
    return (
             <div>
           {/* <Header/>  */}
        <div className="row my-4">
            <div className="listContainer col col-12 col-sm-3">
           <div className=" col-auto ">
            <h4><u>DCE list</u></h4>
            <ul>
            {dceHtml&&console.log('dceHtml: '+JSON.stringify(dceHtml))}

            {dceHtml && dceHtml.map((dce, i)=>{
                return(
                    <div key={i} className="">
                    <h5 className="mt-3"><li className="listRuler mb-1"  data-id={dce.id}> <b>{dce.name.dceName}</b>
                    <hr className="ruler"/> 
                    <button className="btn btn-sm btn-outline-warning mx-1" onClick={editDce}><i class="material-icons mat-icon mt-1">edit</i></button>
                    <Link to={{pathname:"/aspectList/"+uid+"/"+ dce.id, dceFeatures: dce.features, groupFeature: dce.groupFeature, dceNameEdit: dceNameEdit}}>
                    <button className="btn btn-sm btn-outline-danger mx-1" ><i class="material-icons mat-icon mt-1">settings</i></button>
                    </Link>
                    <button className="btn  btn-outline-success btn-sm mx-1" onClick={(e) => { if (window.confirm('Are you sure you wish to delete this DCE?')) deleteDce(e) } }><i class="material-icons mat-icon mt-1">delete_forever</i></button>
                    </li></h5>
                    </div>
                )
                })
            }
        </ul>
</div>
<div className="row my-4">
<div className="col-auto  ">
    <h4><u>Create new DCE</u></h4>
    <div> <button className="btn btn-sm btn-outline-info" onClick={showNewDceForm}> new DCE</button>
</div>
{showNew&&<NewDce showDceList={showDceList} showNewDceForm={showNewDceForm} dceNameList={dceNameList} />}
</div>
</div>
</div>

<div className="col scenarioContainer">
    <ScenarioList dceNameEdit = {dceNameEdit} dceId={dceId} dceFeatures={dceFeatures}  dceDetails={dceDetails}/>
</div>
</div>

</div>
    )
}
