
import { useState, useEffect } from "react";

const OptieA = ({handleClick, sc1, sc2}) => {

const [choiceValue, setChoiceValue] = useState({})

    useEffect(() => {
      if(sc1&&sc2){
      let fValues1 = sc1.data.fValues;
      let fValues2 = sc2.data.fValues;
      const cValue = {pref: fValues1, nonPref: fValues2}
      setChoiceValue(cValue);
      }
    }, [sc1, sc2])
    
    return ( 
        <div className="swiper-slide choose-btn">
				<div className="s10 ">
           {sc1&&<div className="choice-container">
  <p>{sc1.data.scText}</p>
  <img className = "swipe-img choice-container" src={sc1.data.scImg} alt="Image not loaded"
             />
</div>}

                </div>
					{sc1&&<button value='A' onClick={()=>{handleClick(choiceValue)}} className="btn blue darken-2 waves-effect waves-light z-depth-2">Choose</button>}
                 </div>
     );
}
 
export default OptieA;