import React, { useContext, useEffect, useState } from "react";
import { UserContext } from "../providers/UserProvider";
import { pemFirestore } from "../firebase/config";
import { useParams, HashRouter as Router, Route, Link } from "react-router-dom";
import ResultDce from "./ResultDce";

 export default function Dces() {
      
    
    const [dceHtml, setDceHtml] = useState([]);
    const [showNew, setShowNew] = useState(false);
    const [dceNameEdit, setDceNameEdit] = useState('(Click on ICON in DCE list)');
    const [dceFeatures, setDceFeatures] = useState([]);
    const [dceId, setDceId] = useState('');
    const [dceNameList, setDceNameList] = useState([]);
    const [dceDetails, setDceDetails] = useState([])
    const user = useContext(UserContext);
    const uid = user.uid;

//Get list of dce's from database and show on screen
const showDceList = async() => {
    let response = pemFirestore.collection('users').doc(uid).collection('DceList');
    let data = await response.get();
    let tempDocs = [];
    let tempNameList = [];
    data.docs.forEach(doc => {
        tempDocs.push({...doc.data(), id: doc.id});
        tempNameList.push(doc.data().name.dceName);
      });
    setDceHtml(tempDocs) ;
    setDceNameList(tempNameList);
}

//auto trigger showDceList upon first render and when user changes
useEffect(() => {
    showDceList();
  }, [user])


// collect dce id from clicked button and get data from database so dce can be edited.
const resultDce = (e) => {
    let dce;
console.log("e is: "+ e.target.parentElement.parentElement.getAttribute('data-id'));
if(e.target.parentElement.parentElement.getAttribute('data-id')){
dce = e.target.parentElement.parentElement.getAttribute('data-id');
} else{
    dce = e.target.parentElement.getAttribute('data-id');
}
const dceData = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dce).get()
.then((doc)=>{
    const dceName = doc.data().name.dceName;
    const tempDetails = {details: {grouped: doc.data().grouped, groupFeature: doc.data().groupFeature, nrQuestions: doc.data().nrQuestions}}
    setDceNameEdit(dceName);
    setDceDetails(tempDetails);
    setDceFeatures(doc.data().features)
    setDceId(dce);
    console.log('dceDetails onCreate: '+JSON.stringify(tempDetails))
})
}


  //html to show in browser
    return (
             <div>
        <div className="row my-4">
            <div className="listContainer col col-12 col-sm-3">
           <div className=" col-auto ">
            <h4><u>DCE list</u></h4>
            <ul>
            {dceHtml && dceHtml.map((dce, i)=>{
                return(
                    <div key={i} className="">
                    <h5 className="mt-3"><li className="listRuler mb-1"  data-id={dce.id}> <b>{dce.name.dceName}</b>
                    <hr className="ruler"/> 
                    <Link to={{pathname:"/app/"+uid+"/"+ dce.id}}>
                    <button className="btn btn-sm btn-outline-info mx-1" ><i class="material-icons mat-icon mt-1">play_circle_outline</i></button>
                    </Link>
                    <button className="btn btn-sm btn-outline-danger mx-1" onClick={resultDce}><i class="material-icons mat-icon mt-1">poll</i></button>
                    </li></h5>
                    </div>
                )
                })
            }
        </ul>
</div>
                    <div className="row my-4">
                        <div className="col-auto  ">
                            <div> 
                            </div>
                        </div>
                    </div>
</div>

<div className="col scenarioContainer">
    <ResultDce  dceDetails={dceDetails} dceNameEdit = {dceNameEdit} dceId={dceId} dceFeatures={dceFeatures} userId={uid} />
</div>
</div>

</div>
    )
}
